.container {
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.cube {
  width: 400px;
  height: 400px;
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;

}


.side {
	height: 400px;
	width: 400px;
	position: absolute;
	opacity: .5;
}

.top {
	-webkit-transform: rotateX( 90deg ) translateZ( 200px );
	        transform: rotateX( 90deg ) translateZ( 200px );
	background-color: green;
}

.bottom {
	-webkit-transform: rotateX( -90deg ) translateZ( 200px );
	        transform: rotateX( -90deg ) translateZ( 200px );
	background-color: yellow;
}

.front {
	-webkit-transform: rotateX( 0deg ) translateZ( 200px );
	        transform: rotateX( 0deg ) translateZ( 200px );
	background-color: red;
}

.back {
	-webkit-transform: rotateX( -180deg ) translateZ( 200px );
	        transform: rotateX( -180deg ) translateZ( 200px );
	background-color: orange;
}

.left {
	-webkit-transform: rotateY( -90deg ) translateZ( 200px );
	        transform: rotateY( -90deg ) translateZ( 200px );
	background-color: blue;
}

.right {
	-webkit-transform: rotateY( 90deg ) translateZ( 200px );
	        transform: rotateY( 90deg ) translateZ( 200px );
	background-color: purple;
}

.show-top {
	-webkit-transform: translateZ( -200px ) rotateX( -90deg );
	        transform: translateZ( -200px ) rotateX( -90deg );
}

.show-bottom {
	-webkit-transform: translateZ( -200px ) rotateX( 90deg );
	        transform: translateZ( -200px ) rotateX( 90deg );
}

.show-front {
	-webkit-transform: translateZ( -200px ) rotateX( 0deg );
	        transform: translateZ( -200px ) rotateX( 0deg );
}

.show-back {
	-webkit-transform: translateZ( -200px ) rotateX( 180deg );
	        transform: translateZ( -200px ) rotateX( 180deg );
}

.show-left {
	-webkit-transform: translateZ( -200px ) rotateY( -90deg );
	        transform: translateZ( -200px ) rotateY( -90deg );
}

.show-right {
	-webkit-transform: translateZ( -200px ) rotateY( 90deg );
	        transform: translateZ( -200px ) rotateY( 90deg );
}


