input {
  height: 30px;
  width: auto;
  margin: auto;
  border-radius: 5px;
  -webkit-box-shadow: none;
          box-shadow: none;
  display: -ms-flexbox;
  display: flex;
  
}

.border {
  border: 1px solid black;
  height: 80%;
  width: 80%;
  background-color: silver;
  position: absolute;
  -webkit-transform-origin: 50% 50% -200px;
      -ms-transform-origin: 50% 50% -200px;
          transform-origin: 50% 50% -200px;
}

.animator {
  width: 80%;
  height: 80%;
}

#root {
  position: fixed;
  top: 0px;
  bottom: 0px;
  right: 0px;
  left: 0px;

}

.App {
  width: 100%;
  height: 100%;
}



